'use client';

/* --------------------------------------------------
   Booking form – improved UI + fixed order payload + Dynamic import fix
---------------------------------------------------*/
import { useState, useEffect, useMemo } from 'react';
import { format } from 'date-fns';
import { CalendarIcon, Clock } from 'lucide-react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { useRouter } from 'next/navigation';
import dynamic from 'next/dynamic';

import { Button } from '../../../components/ui/button';
import { Calendar } from '../../../components/ui/calendar';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
  FormDescription,
} from '../../../components/ui/form';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from '../../../components/ui/popover';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '../../../components/ui/select';
import { Input } from '../../../components/ui/input';
import { Textarea } from '../../../components/ui/textarea';
import { toast } from '../../../components/ui/sonner';
import { cn } from '../../../lib/utils';
import { createOrder } from '../../../services/createorder';

/* --------------------------------------------------
   Constants & Helpers
---------------------------------------------------*/
const CONTROL_BASE =
  'w-full rounded-lg border border-gray-300 bg-white text-gray-900 placeholder-gray-400 ' +
  'px-4 py-2 leading-tight transition focus:outline-none focus:ring-2 focus:ring-blue-500';

const INPUT_STYLES = CONTROL_BASE;
const TRIGGER_STYLES = `${CONTROL_BASE} flex justify-between items-center`;

const TIMES = [
  '09:00',
  '10:00',
  '11:00',
  '12:00',
  '13:00',
  '14:00',
  '15:00',
  '16:00',
  '17:00',
  '18:00',
  '19:00',
] as const;

type TimeValue = typeof TIMES[number];

/* --------------------------------------------------
   Zod schema
---------------------------------------------------*/
const formSchema = z.object({
  name: z.string().min(2, 'Name must be at least 2 characters.'),
  email: z.string().email('Please enter a valid email address.'),
  phone: z.string().min(10, 'Phone number must be at least 10 digits.'),
  whatsapp: z.string().min(10, 'WhatsApp number must be at least 10 digits.'),
  eventType: z.string({ required_error: 'Please select an event type.' }),
  product: z.string().optional(),
  date: z.date({ required_error: 'Please select a date.' }),
  time: z.enum(TIMES, { required_error: 'Please select a time.' }),
  location: z.string().min(5, 'Please provide the event location.'),
  requirements: z.string().optional(),
});

type FormValues = z.infer<typeof formSchema>;

/* --------------------------------------------------
   Order Data Types
---------------------------------------------------*/
interface OrderResponse {
  id: number;
  status: string;
  [key: string]: unknown;
}

interface MetaData {
  key: string;
  value: string;
}

interface LineItem {
  product_id?: number;
  variation_id?: number;
  sku?: string;
  name: string;
  quantity: number;
  price: string;
  subtotal: string;
  total: string;
  tax_class?: string;
  meta_data?: MetaData[];
}

interface BillingAddress {
  first_name: string;
  last_name: string;
  email: string;
  phone: string;
  address_1?: string;
  address_2?: string;
  city?: string;
  state?: string;
  postcode?: string;
  country?: string;
}

interface ShippingAddress {
  first_name: string;
  last_name: string;
  address_1?: string;
  address_2?: string;
  city?: string;
  state?: string;
  postcode?: string;
  country?: string;
}

interface FeeLine {
  id?: number;
  name: string;
  tax_class?: string;
  tax_status?: string;
  amount: string;
  total: string;
  meta_data?: MetaData[];
}

interface CouponLine {
  id?: number;
  code: string;
  discount: string;
  discount_tax?: string;
  meta_data?: MetaData[];
}

interface RefundLine {
  id?: number;
  reason?: string;
  amount: string;
  refunded_by?: number;
  date_created?: string;
  meta_data?: MetaData[];
}

interface OrderData {
  billing: BillingAddress;
  shipping: ShippingAddress;
  line_items: LineItem[];
  meta_data: MetaData[];
  currency: string;
  status: string;
  payment_method: string;
  payment_method_title: string;
  set_paid: boolean;
  customer_note?: string;
  shipping_total?: string;
  shipping_tax?: string;
  fee_lines?: FeeLine[];
  coupon_lines?: CouponLine[];
  refunds?: RefundLine[];
}

/* --------------------------------------------------
   Hook to get search params safely
---------------------------------------------------*/
function useSearchParamsSafe() {
  const [searchParams, setSearchParams] = useState<URLSearchParams | null>(null);
  
  useEffect(() => {
    // Only access window on client side
    if (typeof window !== 'undefined') {
      setSearchParams(new URLSearchParams(window.location.search));
    }
  }, []);
  
  return searchParams;
}

/* --------------------------------------------------
   Booking Form Component
---------------------------------------------------*/
function BookingFormContent() {
  const router = useRouter();
  const searchParams = useSearchParamsSafe();

  /* ----------------------------------------------
     URL params ➜ form defaults
  ------------------------------------------------*/
  const productParam = searchParams?.get('product') ?? '';
  const dateParam = searchParams?.get('date');
  const timeParam = searchParams?.get('time') ?? '';

  const initialDate = useMemo(() => {
    if (!dateParam) return undefined;
    const d = new Date(dateParam);
    return isNaN(d.getTime()) ? undefined : d;
  }, [dateParam]);

  const isValidTime = (time: string): time is TimeValue => {
    return TIMES.includes(time as TimeValue);
  };

  /* ----------------------------------------------
     React‑Hook‑Form setup
  ------------------------------------------------*/
  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: '',
      email: '',
      phone: '',
      whatsapp: '',
      eventType: '',
      product: productParam,
      date: initialDate,
      time: isValidTime(timeParam) ? timeParam : undefined,
      location: '',
      requirements: '',
    },
    mode: 'onBlur',
  });

  const [isSubmitting, setIsSubmitting] = useState(false);

  /* ----------------------------------------------
     Sync date/time from URL when component mounts
  ------------------------------------------------*/
  useEffect(() => {
    if (initialDate) {
      form.setValue('date', initialDate);
    }
    if (timeParam && isValidTime(timeParam)) {
      form.setValue('time', timeParam);
    }
  }, [initialDate, timeParam, form]);

  /* ----------------------------------------------
     Submit handler
  ------------------------------------------------*/
  const onSubmit = async (values: FormValues) => {
    setIsSubmitting(true);

    try {
      const [firstName, ...rest] = values.name.trim().split(' ');
      const lastName = rest.join(' ') || 'N/A';
      const serviceSku = `SERVICE_${Date.now()}`;

      const orderData: OrderData = {
        billing: {
          first_name: firstName,
          last_name: lastName,
          email: values.email,
          phone: values.phone,
          address_1: values.location,
          city: 'Delhi',
          state: 'Delhi',
          postcode: '110001',
          country: 'IN',
        },
        shipping: {
          first_name: firstName,
          last_name: lastName,
          address_1: values.location,
          city: 'Delhi',
          state: 'Delhi',
          postcode: '110001',
          country: 'IN',
        },
        line_items: [
          {
            sku: serviceSku,
            name: values.product || 'Decoration Service Booking',
            quantity: 1,
            price: '2000.00',
            subtotal: '2000.00',
            total: '2000.00',
            tax_class: '',
            meta_data: [
              { key: 'service_type', value: 'decoration' },
              { key: 'booking_id', value: serviceSku },
            ],
          },
        ],
        meta_data: [
          { key: 'event_type', value: values.eventType },
          { key: 'event_date', value: format(values.date, 'yyyy-MM-dd') },
          { key: 'event_time', value: values.time },
          { key: 'event_location', value: values.location },
          { key: 'whatsapp_number', value: values.whatsapp },
          { key: 'special_requirements', value: values.requirements || '' },
          { key: 'booking_type', value: 'decoration_service' },
          { key: 'submitted_at', value: new Date().toISOString() },
          { key: 'service_sku', value: serviceSku },
          { key: 'customer_name', value: values.name },
          { key: 'customer_email', value: values.email },
          { key: 'customer_phone', value: values.phone },
        ],
        currency: 'INR',
        status: 'pending',
        payment_method: 'booking_request',
        payment_method_title: 'Booking Request',
        set_paid: false,
        customer_note: values.requirements || '',
        shipping_total: '0.00',
        shipping_tax: '0.00',
        fee_lines: [],
        coupon_lines: [],
        refunds: [],
      };

      const order: OrderResponse = await createOrder(orderData);

      toast.success('Booking submitted successfully!', {
        description: `Your booking request has been submitted with order ID: ${order.id}. We'll contact you shortly to confirm your booking.`,
      });

      form.reset();
      setTimeout(() => router.push('/'), 3000);
    } catch (error) {
      console.error('Order creation error:', error);
      toast.error('Failed to submit booking', {
        description:
          'There was an error submitting your booking. Please try again or contact us directly.',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="container mx-auto px-4 py-12">
      <div className="max-w-3xl mx-auto">
        {/* Heading block */}
        <div className="text-center mb-10">
          <h1 className="text-3xl md:text-4xl font-bold mb-4 text-blue-700">
            Book Your Decoration Service
          </h1>
          <p className="text-gray-600">
            Fill out the form below to request a booking for your special event
          </p>
          <div className="mt-4 p-4 bg-pink-200 rounded-lg inline-block shadow-sm">
            <p className="text-blue-700 text-lg font-semibold">
              Starting from ₹2,000
            </p>
            <p className="text-sm text-blue-700/80">
              Final pricing depends on your requirements
            </p>
          </div>
          {productParam && (
            <div className="mt-4 p-2 bg-pink-100 rounded-md inline-block">
              <p className="text-blue-700">
                Product: <span className="font-medium">{productParam}</span>
              </p>
            </div>
          )}
        </div>

        {/* Form card */}
        <div className="bg-blue-50 rounded-xl p-8 shadow-md hover:shadow-lg transition border border-blue-100">
          <Form {...form}>
            <form
              onSubmit={form.handleSubmit(onSubmit)}
              className="space-y-6"
              autoComplete="off"
            >
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Name */}
                <FormField
                  control={form.control}
                  name="name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-gray-800 font-medium">
                        Full Name
                      </FormLabel>
                      <FormControl>
                        <Input
                          className={INPUT_STYLES}
                          placeholder="Enter your full name"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                {/* Email */}
                <FormField
                  control={form.control}
                  name="email"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-gray-800 font-medium">
                        Email
                      </FormLabel>
                      <FormControl>
                        <Input
                          className={INPUT_STYLES}
                          placeholder="Enter your email address"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                {/* Phone */}
                <FormField
                  control={form.control}
                  name="phone"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-gray-800 font-medium">
                        Phone Number
                      </FormLabel>
                      <FormControl>
                        <Input
                          className={INPUT_STYLES}
                          placeholder="Enter your phone number"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                {/* WhatsApp */}
                <FormField
                  control={form.control}
                  name="whatsapp"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-gray-800 font-medium">
                        WhatsApp Number
                      </FormLabel>
                      <FormControl>
                        <Input
                          className={INPUT_STYLES}
                          placeholder="Enter your WhatsApp number"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                {/* Product */}
                {productParam && (
                  <FormField
                    control={form.control}
                    name="product"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel className="text-gray-800 font-medium">
                          Selected Product
                        </FormLabel>
                        <FormControl>
                          <Input
                            className="bg-gray-100 text-gray-800"
                            readOnly
                            {...field}
                          />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                )}

                {/* Event type */}
                <FormField
                  control={form.control}
                  name="eventType"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-gray-800 font-medium">
                        Event Type
                      </FormLabel>
                      <Select
                        onValueChange={field.onChange}
                        defaultValue={field.value}
                      >
                        <FormControl>
                          <SelectTrigger className={TRIGGER_STYLES}>
                            <SelectValue placeholder="Select event type" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent className="max-h-60 overflow-y-auto">
                          <SelectItem value="birthday">Birthday Party</SelectItem>
                          <SelectItem value="wedding">Wedding</SelectItem>
                          <SelectItem value="corporate">Corporate Event</SelectItem>
                          <SelectItem value="anniversary">Anniversary</SelectItem>
                          <SelectItem value="festival">Festival Decoration</SelectItem>
                          <SelectItem value="other">Other</SelectItem>
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                {/* Location */}
                <FormField
                  control={form.control}
                  name="location"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-gray-800 font-medium">
                        Event Location
                      </FormLabel>
                      <FormControl>
                        <Input
                          className={INPUT_STYLES}
                          placeholder="Enter event location"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                {/* Date */}
                <FormField
                  control={form.control}
                  name="date"
                  render={({ field }) => (
                    <FormItem className="flex flex-col">
                      <FormLabel className="text-gray-800 font-medium">
                        Event Date
                      </FormLabel>
                      <Popover>
                        <PopoverTrigger asChild>
                          <FormControl>
                            <Button
                              variant="outline"
                              className={cn(
                                `${TRIGGER_STYLES} pl-3 text-left font-normal`,
                                !field.value && 'text-muted-foreground'
                              )}
                            >
                              {field.value ? (
                                format(field.value, 'PPP')
                              ) : (
                                <span>Pick a date</span>
                              )}
                              <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                            </Button>
                          </FormControl>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="start">
                          <Calendar
                            mode="single"
                            selected={field.value}
                            onSelect={field.onChange}
                            disabled={(date) => date < new Date()}
                            initialFocus
                            className="p-3 pointer-events-auto"
                          />
                        </PopoverContent>
                      </Popover>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                {/* Time */}
                <FormField
                  control={form.control}
                  name="time"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-gray-800 font-medium">
                        Event Time
                      </FormLabel>
                      <Select onValueChange={field.onChange} value={field.value}>
                        <FormControl>
                          <SelectTrigger className={TRIGGER_STYLES}>
                            <SelectValue placeholder="Select a time" />
                            <Clock className="h-4 w-4 opacity-50" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent className="max-h-60 overflow-y-auto">
                          {TIMES.map((t) => (
                            <SelectItem key={t} value={t}>
                              {`${parseInt(t, 10) % 12 || 12}:00 ${parseInt(t, 10) >= 12 ? 'PM' : 'AM'}`}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              {/* Requirements */}
              <FormField
                control={form.control}
                name="requirements"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="text-gray-800 font-medium">
                      Special Requirements
                    </FormLabel>
                    <FormControl>
                      <Textarea
                        className={`min-h-32 ${INPUT_STYLES}`}
                        placeholder="Tell us about any special requirements or preferences"
                        {...field}
                      />
                    </FormControl>
                    <FormDescription>
                      Include any theme preferences, color schemes, or specific decorations.
                    </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              {/* Submit */}
              <Button
                type="submit"
                size="lg"
                className="w-full bg-blue-600 hover:bg-blue-700 text-white"
                disabled={isSubmitting}
              >
                {isSubmitting ? 'Submitting...' : 'Submit Booking Request'}
              </Button>
            </form>
          </Form>
        </div>
      </div>
    </div>
  );
}

/* --------------------------------------------------
   Loading component
---------------------------------------------------*/
function BookingPageLoading() {
  return (
    <div className="container mx-auto px-4 py-12">
      <div className="max-w-3xl mx-auto">
        <div className="text-center mb-10">
          <div className="h-8 bg-gray-200 rounded-md w-96 mx-auto mb-4 animate-pulse"></div>
          <div className="h-4 bg-gray-200 rounded-md w-64 mx-auto animate-pulse"></div>
        </div>
        <div className="bg-blue-50 rounded-xl p-8 shadow-md">
          <div className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {[...Array(6)].map((_, i) => (
                <div key={i} className="space-y-2">
                  <div className="h-4 bg-gray-200 rounded w-24 animate-pulse"></div>
                  <div className="h-10 bg-gray-200 rounded animate-pulse"></div>
                </div>
              ))}
            </div>
            <div className="space-y-2">
              <div className="h-4 bg-gray-200 rounded w-32 animate-pulse"></div>
              <div className="h-24 bg-gray-200 rounded animate-pulse"></div>
            </div>
            <div className="h-12 bg-gray-200 rounded animate-pulse"></div>
          </div>
        </div>
      </div>
    </div>
  );
}

/* --------------------------------------------------
   Dynamic import with no SSR
---------------------------------------------------*/
const DynamicBookingForm = dynamic(() => Promise.resolve(BookingFormContent), {
  ssr: false,
  loading: () => <BookingPageLoading />,
});

/* --------------------------------------------------
   Main component
---------------------------------------------------*/
export default function BookingPage() {
  return (
    <div className="min-h-screen bg-yellow-50">
      <DynamicBookingForm />
    </div>
  );
}